const generateCaptionFromFileName = (fileName: string) => {
  // Extrai o nome do arquivo sem a extenso
  let baseName = fileName.split("/").pop()?.split(".").shift() || "";

  // Remove o nmero "1-" se estiver no incio do nome
  if (baseName.startsWith("1-")) {
    baseName = baseName.substring(2);
  }

  // Substitui hfens por espaos e capitaliza cada palavra
  return baseName
    .split("-")
    .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
    .join(" ");
};

export { generateCaptionFromFileName };
